<template>
  <div class="singListPage" v-if="playlist">
    <div class="head">
      <div class="img">
        <img :src="playlist.frontPicUrl" alt="" />
      </div>
      <div class="desc">
        <div class="name">
          <span class="spName">{{ playlist.AdShareContent }}</span>
        </div>
        <div class="author">
          <span class="name-wrapper">
            <span class="time">{{ playlist.period }}更新</span>
          </span>
        </div>
        <div class="play-wrapper">
          <van-button
            round
            type="primary"
            icon="play"
            color="#1ed2a8"
            @click="playAll"
            >播放全部</van-button
          >
          <van-button type="default" round icon="send-gift-o">收藏</van-button>
          <van-button type="default" round icon="share-o">分享</van-button>
          <van-button type="default" round icon="down">下载全部</van-button>
        </div>

        <div class="info">
          <OverText :text="playlist.intro" />
        </div>
      </div>
    </div>
    <div class="body">
      <div class="body-top">
        <div class="text">
          <span class="text1 ml">歌曲列表</span>
        </div>
      </div>
      <div class="list">
        <div class="list-top">
          <div class="top-item operator">操作</div>
          <div class="top-item title">标题</div>
          <div class="top-item singer">歌手</div>
          <div class="top-item cd">专辑</div>
          <div class="top-item time">时间</div>
        </div>
        <div
          class="list-top sing"
          v-for="(item, index) in playlist.song"
          :key="item.singerMid"
          @click="playSong(item)"
          :class="item.songId == playingList.id ? 'active' : ''"
        >
          <div class="top-item operator">
            <span>{{ String(index + 1).padStart(2, "0") }}</span>
            <van-icon name="like-o" />
            <van-icon name="down" />
          </div>
          <div class="top-item title">
            {{ item.title }}
            <span class="addition" style="color: #07c160"> VIP</span>
            <span class="addition" style="color: #ff976a"> SQ</span>
            <span> <i class="iconfont icon-mv"></i></span>
          </div>
          <div class="top-item singer">
            <span>{{ item.singerName }}</span>
          </div>
          <div class="top-item cd">中国好声音</div>
          <div class="top-item time">
            {{
              String(Math.floor(260 / 60)).padStart(2, "0") +
              ":" +
              String(Math.floor(260 % 60)).padStart(2, "0")
            }}
          </div>
        </div>
      </div>
    </div>
    <div class="player-space"></div>
  </div>
</template>

<script>
import { onBeforeMount, reactive, toRefs, watch } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import OverText from "../components/overText.vue";
export default {
  components: {
    OverText,
  },
  setup(props, ctx) {
    const state = reactive({
      playlist: {},
      songUrl: "",
    });
    const switchBar = () => {
      state.show = !state.show;
    };
    const router = useRouter();
    const id = router.currentRoute._value.query.id;
    const store = useStore();
    const playAll = () => {
      for (let i = state.playlist.song.length - 1; i >= 0; i--) {
        playSong(state.playlist.song[i]);
      }
      setTimeout(() => {
        ctx.emit("playSong", true);
      }, 0);
    };
    const playSong = (e) => {
      console.log(e);
      for (let i = 0; i < store.state.playList.length; i++) {
        if (store.state.playList[i].id == e.songId) {
          store.commit("setPlayIndex", i);
          return;
        }
      }
      let obj = {};
      obj.id = e.songId;
      obj.songName = e.title;
      obj.author = e.singerName;
      obj.interval = Math.floor(e.songId / 1000);
      // obj.songmid = e.
      obj.coverPic = e.cover;

      obj.songUrl = store.state.playList.length
        ? store.state.playList[
            Math.floor(Math.random() * store.state.playList.length)
          ].songUrl
        : "http://isure.stream.qqmusic.qq.com/M500004adg8z18YoUI.mp3?guid=227137925&vkey=005EABF7BA8543EA705F978E22A3869A6219D34C32A6BC24E7E2BC778688F5F5960547B3E692B39B1B12CC4255A5AF947083473C1F153760&uin=&fromtag=66";
      store.commit("setPlayList", obj);
      setTimeout(() => {
        ctx.emit("playSong", true);
      }, 1000);
    };
    onBeforeMount(() => {
      axios({
        url: `/api//getRanks?topId=${id}`,
        withCredentials: true, //关键
      }).then((res) => {
        state.playlist = res.data.response.req_1.data.data;
        console.log(res.data.response.req_1.data.data);
      });
    });
    return {
      ...toRefs(state),
      playSong,
      switchBar,
      playAll,
    };
  },
  emits: ["playSong"],
  computed: {
    playingList() {
      const store = useStore();
      watch(
        () => store.state.playList,
        (newVal) => {
          store.state.playList = newVal
        },
        { immediate: true, deep: true }
      );
      watch(
        () => store.state.playIndex,
        (newVal) => {
         store.state.playIndex = newVal
        },
        { immediate: true, deep: true }
      );

      return store.state.playList[store.state.playIndex] || { id: 0 };
    },
  },
};
</script>

<style lang="less" scoped>
.singListPage {
  width: 1042px;
  .head {
    width: 100%;
    margin: 35px;
    display: flex;
    .img {
      img {
        width: 210px;
        border-radius: 10px;
      }
    }
    .desc {
      margin-left: 50px;
      .name {
        .sp1 {
          display: inline-block;
          padding: 0px 3px;
          color: #1ed2a8;
          border: 1px solid #1ed2a8;
        }
        .spName {
          margin-left: 10px;
          font-size: 22px;
          font-weight: bold;
        }
      }
      .author {
        margin-top: 11px;
        position: relative;
        img {
          border-radius: 50%;
          width: 25px;
        }
        .identityIcon {
          width: 11px;
          position: absolute;
          left: 16px;
          bottom: 3px;
        }
      }
      .name-wrapper {
        margin-left: 10px;
        position: relative;
        bottom: 6px;
        .authorName {
          font-size: 10px;
          color: #439ac1;
        }
        .time {
          font-size: 10px;
          margin-left: 4px;
          color: #646566;
        }
      }
      .play-wrapper {
        display: flex;
        margin-top: 60px;
        button {
          height: 30px;
          margin-left: 10px;
        }
        .tag {
          font-size: 14px;
          p {
            .spTags {
              color: #5091ca;
            }
          }
        }
        .info {
          font-size: 14px;
          width: 700px;
          height: 62px;
          overflow: hidden;
        }
      }
      .info {
        margin-top: 15px;
        margin-left: 13px;
      }
    }
  }
  .body {
    .body-top {
      display: flex;
      font-size: 15px;
      justify-content: space-between;
      margin: 20px 30px;
      .text {
        font-size: 18px;
        font-weight: bold;
        cursor: pointer;
        .text1 {
        }
        .text2 {
          margin-left: 30px;
        }
        .ml {
          font-size: 15px;
          font-weight: normal;
        }
      }
    }
    .list {
      .list-top {
        display: flex;
        // justify-content: space-around;
        color: #969799;
        font-size: 14px;
        margin: 0 16px;
        .top-item {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          height: 50px;
          line-height: 50px;
        }
        .top-item:hover {
          background-color: #dcdee0;
        }
        .operator {
          text-align: end;
          width: 130px;
          margin-right: 14px;
        }
        .title {
          width: 400px;
          height: 50px;
          line-height: 50px;
          display: flex;
          .addition {
            display: block;
            height: 15px;
            font-size: 10px;
            line-height: 15px;
            margin-left: 8px;
            margin-top: 17px;
            border: 1px solid;
            padding: 0 3px;
            box-sizing: border-box;
          }
          .icon-mv {
            display: block;
            margin-left: 8px;
          }
        }
        .singer {
          width: 200px;
        }
        .cd {
          width: 256px;
        }
        .time {
          width: 160px;
        }
      }
      .active {
        color: #1ed2a9 !important;
      }
      .sing {
        color: #111;
        .operator {
          color: #afb0b0;
          i {
            font-size: 20px;
            margin: 0 8px;
          }
        }
      }
      .sing:nth-child(2n) {
        background-color: #f0f1f2;
      }
      .sing:hover {
        background-color: #dcdee0;
      }
    }
    .comment {
      padding: 31px;
      background-color: #f7f8fa;
      p {
        margin-top: 15px;
      }
      .comment-item {
        margin-left: -20px;
      }
    }
  }
}
</style>